<template>
    <div class="main">
        <div class="header">
            <div
                :class="{
                    'music': true,
                    'music-close': !music,
                    'music-open': music,
                }"
                @click="closeMusic"
            />
            <p
                class="record"
                @click="goRecord()">翻倍记录</p>
            <p class="ante-count">今日可抽<span>{{ leftChance }}</span>次</p>
        </div>
        <turn-table
            ref="turntable"
            v-model="luckyId"
            :awards="awards"
            @start="start"
            @stop="stop"/>
        <div
            v-show="tip"
            class="tip-ante">{{ tip }}</div>
        <div class="footer">
            <p class="coin">我的金币余量 <span class="left-coin">{{ myCoin }}</span></p>
            <div class="wager">
                <div class="change">
                    <span class="change-item change-tip">当前下注</span>
                    <span
                        ref="reduceWager"
                        :style="{
                            visibility: reduceWagerStatus,
                        }"
                        @click="reduceWager()"
                        class="change-item reduce"><i /></span>
                    <span class="change-item wager-coin">{{ currentAnte }}</span>
                    <span
                        ref="addWager"
                        @click="addWager()"
                        :style="{
                            visibility: addWagerStatus,
                        }"
                        class="change-item add">+</span>
                </div>
                <div
                    @click="allIn()"
                    class="all-in"/>
            </div>
            <div
                @click="openShareDialog('btn')"
                class="share">分享多抽一次</div>
            <div class="guide">
                <p class="guide-header">- 活动规则 -</p>
                <!-- <p class="guide-line"/> -->
                <ol class="guide-list">
                    <li>1、 拥有金币的登录用户可参与活动。每天固定翻倍次数为15次，分享增加1次机会。</li>
                    <li>2、 每局下注最低500金币，最高8000金币。</li>
                    <li>3、 活动期间，翻倍系数可能会有变更，具体见转盘显示。</li>
                    <li>4、 所获得的金币将自动进入到您的钱包账户，请在【翻倍记录】中查看</li>
                    <li>5、 如发现有盗刷金币用户，将封禁您的账号，必要时将追究法律责任。</li>
                    <li>6、 本活动解释权归想看APP所有</li>
                </ol>
            </div>
        </div>
        <gray-mask
            @click="openDialog('')"
            :show="dialogKey">
            <lottery-dialog
                :title="dialog.title"
                :description="dialog.description"
                :button-text="dialog.buttonText"
                @btn-click="dialogClickHandler" />
        </gray-mask>
        <gray-mask
            @click="closeShareDialog()"
            :show="showShare">
            <div class="share-dialog">
                <div
                    @click="share('wechat')"
                    class="share-item">
                    <span class="wechat"/>
                    <p>微信</p>
                </div>
                <!-- <div
                    @click="share('moment')"
                    class="share-item">
                    <span class="moment"/>
                    <p>朋友圈</p>
                </div> -->
                <div
                    @click="share('qq')"
                    class="share-item">
                    <span class="qq"/>
                    <p>QQ</p>
                </div>
            </div>
        </gray-mask>
        <gray-mask :show="!isLogin">
            <div class="login">
                <div class="login-icon" />
                <p>登录后</p>
                <p>才能参与金币翻倍哦</p>
                <div
                    class="login-btn"
                    @click="login"/>
            </div>
        </gray-mask>
        <gray-mask :show="showToast">
            <div
                v-if="luckyItem.choiceDesc <= 1"
                class="award-tip"
            >
                <div class="award-coin">{{ latestChange }}</div>
                <div class="award-choicedesc">{{ luckyItem.choiceDesc }}</div>
            </div>
            <div
                v-if="luckyItem.choiceDesc > 1"
                class="big-award-tip"
            >
                <div class="big-award-coin">{{ latestChange.replace('+', '') }}</div>
                <div class="big-award-choicedesc">{{ luckyItem.choiceDesc }}</div>
            </div>
        </gray-mask>
        <gray-mask
            class="location-gray"
            @click="closeLocationTip"
            :show="isLogin && locationTip">
            <div
                class="location-tip"
            >
                <div
                    class="location-tip-close"
                />
            </div>
        </gray-mask>
    </div>
</template>

<script>
import * as lottery from '@/api/lottery';
import { shareCodeTextToWechat } from '@/utils/share';
import {
    toast,
    doLogin,
    isTakeOverBackByWeb,
    quitCurrentWebview,
    getSharedPrefBoolean } from '@/utils/jsApiV2/client';
import { shareWithSocialType } from '@/utils/jsApiV2/act';
import { addCustomEventListener } from '@/utils/event';
import { set, get } from '@/utils/cache';
import GrayMask from '@/components/GrayMask';
import TurnTable from './turntable';
import LotteryDialog from './Dialog';
import { dialogMap } from './config';

export default {
    name: 'Lottery',
    components: {
        TurnTable,
        LotteryDialog,
        GrayMask,
    },
    data() {
        return {
            luckyId: NaN,
            dialogKey: '',
            awards: [
                { choiceId: 1, choiceDesc: '3' },
                { choiceId: 2, choiceDesc: '1.5' },
                { choiceId: 3, choiceDesc: '1.3' },
                { choiceId: 4, choiceDesc: '1.1' },
                { choiceId: 5, choiceDesc: '0.9' },
                { choiceId: 6, choiceDesc: '0.7' },
                { choiceId: 7, choiceDesc: '0.4' },
                { choiceId: 8, choiceDesc: '0.2' },
            ],
            anteMax: 0,
            anteMin: 0,
            currentAnte: 200,
            leftChance: 0,
            latestChange: '+0', // 最后一次抽奖的金币变化
            myCoin: 0,
            nextCoin: 0,
            dialogMap,
            showShare: false,
            showToast: false, // 中奖的toast
            isTodayShared: false, // 今天是否分享过
            isLogin: true,
            tip: '', // 下注提示
            turnTableStop: true, // 转盘是否停止
            isNew: false, // 第一次下注抽奖
            music: true,
            clickAudio: new Audio('https://ts.market.mi-img.com/download/Browser/077215fdaf955f909d9ab39f8929ed42e6741fafa/click.mp3'),
            roundAudio: new Audio('https://ts.market.mi-img.com/download/Browser/0715c477baa3388bd420961227b7d5b987643ef80/round.mp3'),
            locationTip: false,
            showTipTimmer: 0,
        };
    },
    computed: {
        dialog() {
            return this.dialogMap[this.dialogKey] || {};
        },
        luckyItem() {
            return this.awards.filter(v => v.choiceId === this.luckyId)[0] || {};
        },
        reduceWagerStatus() {
            return this.currentAnte > this.anteMin ? 'visible' : 'hidden';
        },
        addWagerStatus() {
            return this.currentAnte < Math.min(this.anteMax, this.myCoin)
                ? 'visible' : 'hidden';
        },
    },
    methods: {
        clickMusicPlay() {
            const voiceSwitch = getSharedPrefBoolean('news_settings_pref', 'receive_coin_voice_switch', true);
            if (this.music && voiceSwitch) {
                this.clickAudio.play();
            }
        },
        roundMusicPlay() {
            const voiceSwitch = getSharedPrefBoolean('news_settings_pref', 'receive_coin_voice_switch', true);
            if (this.music && voiceSwitch) {
                this.roundAudio.loop = true;
                this.roundAudio.play();
            }
        },
        musicPause() {
            try {
                this.roundAudio.pause();
                this.clickAudio.pause();
            } catch (e) {
                console.log(e);
            }
        },
        // 转盘点击回调
        start() {
            if (!this.turnTableStop || this.showToast) {
                return;
            }

            this.clickMusicPlay();
            if (this.leftChance <= 0) { // 没有机会
                this.openDialog(this.isTodayShared ? 'noCount' : 'share');
            } else if (this.myCoin < this.currentAnte) { // 金币不足
                this.openDialog('nocoin');
            } else {
                setTimeout(() => {
                    this.roundMusicPlay();
                }, 500);

                this.$refs.turntable.start();
                this.turnTableStop = false;
                lottery.drawlottery({ ante: this.currentAnte }).then((data) => {
                    this.luckyId = data.choiceId;
                    this.nextCoin = data.myCoin;
                    this.leftChance = data.leftChance;
                    this.latestChange = data.award > 0 ? `+${data.award}` : `-${data.award}`;
                    this.isNew = false;
                }).catch((data) => {
                    this.luckyId = -1;
                    toast(data.msg || '抽奖失败，请再抽一次');
                });
            }
            this.$o2o({
                e_c: '大转盘',
                e_a: '点击',
                e_n: '开始翻倍',
            });
        },
        // 转盘停止时的回调
        stop() {
            this.musicPause();
            if (this.luckyId !== -1) {
                this.myCoin = this.nextCoin;
                if (this.myCoin < this.currentAnte) {
                    this.currentAnte = Math.max(this.anteMin, this.myCoin);
                }
                this.showToast = true;
                setTimeout(() => {
                    this.showToast = false;
                }, 1500);
            }
            this.turnTableStop = true;
        },
        goRecord() {
            this.$o2o({
                e_c: '大转盘',
                e_a: '点击',
                e_n: '翻倍记录',
            });
            const url = `${location.origin}/mobile-v2/act/lottery/record`;
            location.href = `bikan://web.goto/commonWebView?url_encode=${encodeURIComponent(url)}`;
        },
        reduceWager() {
            const nextAnte = this.currentAnte - this.anteMin;
            if (nextAnte < this.anteMin) {
                this.showTip(`最少下注${this.anteMin}金币`);
            }
            this.currentAnte = Math.max(nextAnte, this.anteMin);
            this.$o2o({
                e_c: '大转盘',
                e_a: '点击',
                e_n: '减注',
            });
        },
        addWager() {
            const nextAnte = this.currentAnte + this.anteMin;
            if (this.isNew) {
                this.showTip(`先押注${this.anteMin}金币试试吧`);
                return;
            }
            if (nextAnte > this.anteMax && this.myCoin > this.anteMax) {
                this.showTip(`最多下注${this.anteMax}金币`);
            }
            this.currentAnte = Math.min(nextAnte, this.anteMax, this.myCoin);
            this.$o2o({
                e_c: '大转盘',
                e_a: '点击',
                e_n: '加注',
            });
        },
        allIn() {
            if (this.isNew) {
                this.showTip(`先押注${this.anteMin}金币试试吧`);
                return;
            }
            if (this.myCoin >= this.anteMax) { // 提示最多押注
                this.showTip(`最多下注${this.anteMax}金币`);
                this.currentAnte = this.anteMax;
            } else if (this.myCoin < this.anteMin) { // 金币不足
                this.openDialog('nocoin');
            } else { // 金币低于最大押注
                this.currentAnte = this.myCoin;
            }
            this.$o2o({
                e_c: '大转盘',
                e_a: '点击',
                e_n: '全压',
            });
        },
        // 开启弹窗 dialog: nocoin没金币了、noCount没次数了、share分享加一次
        openDialog(dialogKey) {
            // if (dialogKey) {
            //     isTakeOverBackByWeb(true);
            // } else {
            //     isTakeOverBackByWeb(false);
            // }
            this.dialogKey = dialogKey;
            this.$o2o({
                e_c: '大转盘',
                e_a: '曝光',
                e_n: this.dialog.stat,
            });
        },
        // 弹窗中的按钮点击
        dialogClickHandler() {
            isTakeOverBackByWeb(false);

            if (this.dialogKey === 'share') {
                this.openShareDialog('dialog');
            } else {
                // 其他两个都是赚金币
                this.$o2o({
                    e_c: '大转盘',
                    e_a: '点击',
                    e_n: '弹窗-赚金币',
                }, () => {
                    location.href = `${location.origin}/mobile-v2/task`;
                }, () => {
                    location.href = `${location.origin}/mobile-v2/task`;
                });
            }
            this.openDialog('');
        },
        share(channel) {
            lottery.share().then((data) => {
                const shareMap = {
                    qq: '1',
                    wechat: '2',
                    moment: '3',
                };
                this.leftChance = data.leftChance;
                this.isTodayShared = true;
                const url = `${location.origin}/mobile-v2/act/lottery/share?share=lottery`;
                if (shareMap[channel] === '2') {
                    shareCodeTextToWechat(url);
                } else {
                    shareWithSocialType({
                        title: '幸运大转盘，好运转出来~',
                        url,
                        text: '千万现金红包免费抽，速来参与>>',
                        thumbUrl: 'https://ts.market.mi-img.com/download/Browser/0dfcc57b54685de3ec6acf774097c1d5f67406458/logo_to_share.png',
                        socialType: shareMap[channel],
                    });
                }
            }).catch((data) => {
                toast((data.msg || '调起分享失败，再试一次吧'));
            });
            this.$o2o({
                e_c: '大转盘',
                e_a: '点击',
                e_n: '分享',
                e_x: {
                    share_channel: channel,
                },
            });
        },
        openShareDialog(source) {
            // isTakeOverBackByWeb(true);
            this.showShare = true;
            if (source === 'btn') {
                this.$o2o({
                    e_c: '大转盘',
                    e_a: '点击',
                    e_n: '分享多抽一次',
                });
            } else if (source) {
                this.$o2o({
                    e_c: '大转盘',
                    e_a: '点击',
                    e_n: '弹窗-分享',
                });
            }
        },
        closeShareDialog() {
            // isTakeOverBackByWeb(false);
            this.showShare = false;
        },
        login() {
            try {
                if ('bindWx' in window.miui) {
                    window.miui.bindWx('lotteryLoginCallback');
                } else {
                    doLogin('lotteryLoginCallback');
                }
            } catch (e) {
                console.log(e);
            }
        },
        getData() {
            lottery.get().then((data) => {
                this.awards = data.choiceList;
                this.anteMax = data.anteMax;
                this.anteMin = data.anteMin;
                this.leftChance = data.leftChance;
                this.myCoin = data.myCoin;
                this.isTodayShared = data.isTodayShared;
                this.isLogin = data.isLogin;
                this.isNew = data.isNew;
                this.currentAnte = this.anteMin;
                if (!this.isLogin) {
                    this.$o2o({
                        e_c: '大转盘',
                        e_a: '曝光',
                        e_n: '登录弹窗',
                    });
                }

                this.$root.hidePlaceholder();
            }).catch(() => {
                this.$router.replace({ name: 'error' });
            });
        },
        backKeyHandler() {
            if (this.locationTip) {
                this.closeLocationTip();
                isTakeOverBackByWeb(false);
            }

            if (!this.locationTip && !this.showShare && !this.showToast && !this.dialogKey) {
                this.showLocationTip();
            }

            this.closeShareDialog();
            this.openDialog('');
        },
        showTip(tip) {
            this.tip = tip;
            clearTimeout(this.showTipTimmer);
            this.showTipTimmer = setTimeout(() => {
                this.tip = '';
            }, 1500);
        },
        closeMusic() {
            this.music = !this.music;
            this.musicPause();
        },
        closeLocationTip() {
            this.locationTip = false;
        },
        showLocationTip() {
            if (get('lotteryEnterTip') === '1') {
                quitCurrentWebview();
            }

            set('lotteryEnterTip', '1');
            this.locationTip = true;
        },
    },
    created() {
        this.getData();
        isTakeOverBackByWeb(true);
        addCustomEventListener('backKey', this.backKeyHandler);
        addCustomEventListener('blur', this.musicPause);
        window.lotteryLoginCallback = (success) => {
            if (success) {
                this.getData();
                this.$o2o({
                    e_c: '大转盘',
                    e_a: '成功',
                    e_n: '登录',
                });
            }
        };
        this.$o2o({
            e_c: '大转盘',
            e_a: '曝光',
            e_n: '首页',
        });
    },
    destroyed() {
        clearTimeout(this.showTipTimmer);
    },
};
</script>


