<template>
    <div
        :class="{
            turntable: true,
            'turntable-normal': turntableLight,
            'turntable-reverse': !turntableLight,
        }"
    >
        <div class="container">
            <div
                :class="{
                    awrads: true,
                    'table-rotate': status,
                }"
                ref="turntable">
                <div
                    :class="{
                        award: true,
                        award_active: luckyIndex === i && end,
                    }"
                    v-for="(award, i) in awards"
                    :key="award.choiceId"
                    :style="{transform: `rotate(${i * 45}deg)`}">
                    <template v-if="award.choiceDesc">
                        {{ award.choiceDesc }}
                    </template>
                    <template v-else>
                        <img :src="award.url">
                    </template>
                </div>
            </div>
            <div
                :class="{
                    button: true,
                    button_scale: awards[0].url,
                }"
                @click="run">
                <div
                    :class="{
                        'button-scale': true,
                        'button-scale-animation': !isRotating,
                    }"
                />
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Turntable',
    model: {
        prop: 'luckyId',
        event: 'update',
    },
    props: {
        awards: {
            type: Array,
            required: true,
        },
        luckyId: {
            type: Number,
            required: true,
        },
    },
    data() {
        return {
            status: false,
            angle: 0, // 结束时的角度
            deltaAngle: 720, // 变速角度
            deltaTime: 2000, // 变速时间
            endTime: 0, // 结束时的时间
            luckyIndex: -1, // 奖项索引
            end: false, // 是否结束
            turntableLight: true,
            timmer: 0,
            isRotating: false,
        };
    },
    created() {
        this.$nextTick(() => {
            this.timmer = setInterval(() => {
                this.turntableLight = !this.turntableLight;
            }, 500);
        });
    },
    destroyed() {
        clearInterval(this.timmer);
    },
    watch: {
        luckyId(v) {
            // 分配到了奖项，并且已经正常匀速转动。
            if (!isNaN(v) && this.status) {
                this.preSlowDown();
            }
        },
    },
    computed: {
        halfAcceleration() {
            const v = 360 / 500; // 最大速度(s/t), css匀速动画时间t
            return (this.deltaAngle - (v * this.deltaTime)) / (this.deltaTime * this.deltaTime);
        },
    },
    methods: {
        run() {
            this.$emit('start');
        },
        start() {
            this.end = false;
            this.$emit('update', NaN);
            this.endTime = Date.now();
            this.isRotating = true;
            requestAnimationFrame(this.speedUp);
        },
        preSlowDown() {
            this.luckyIndex = -1;
            for (let i = 0; i < this.awards.length; i += 1) {
                if (this.awards[i].choiceId === this.luckyId) {
                    this.luckyIndex = i;
                    break;
                }
            }
            this.status = false;
            if (this.luckyIndex === -1) {
                this.angle = 22;
            } else {
                this.angle = 360 - (this.luckyIndex * 45); // 转盘反着转的角度等于选项块正向转动角度
            }
            this.endTime = Date.now() + this.deltaTime;
            requestAnimationFrame(this.slowDown);
        },
        slowDown() {
            const lastTime = this.endTime - Date.now();
            if (lastTime > 0) {
                this.$refs.turntable.style['-webkit-transform'] = `rotate(${this.angle + (this.halfAcceleration * lastTime * lastTime)}deg)`;
                this.$refs.turntable.style.transform = `rotate(${this.angle + (this.halfAcceleration * lastTime * lastTime)}deg)`;
                requestAnimationFrame(this.slowDown);
            } else {
                this.$refs.turntable.style['-webkit-transform'] = `rotate(${this.angle}deg)`;
                this.$refs.turntable.style.transform = `rotate(${this.angle}deg)`;
                this.$emit('stop');
                this.isRotating = false;
                this.end = true;
            }
        },
        speedUp() {
            const deltaTime = Date.now() - this.endTime;
            if (deltaTime < 1000) {
                // 加速2倍加速度 1s加速
                this.$refs.turntable.style['-webkit-transform'] = `rotate(${this.angle + (0.00036 * deltaTime * deltaTime)}deg)`;
                this.$refs.turntable.style.transform = `rotate(${this.angle + (0.00036 * deltaTime * deltaTime)}deg)`;
                requestAnimationFrame(this.speedUp);
            } else {
                this.status = true;
                this.$refs.turntable.style['-webkit-transform'] = '';
                this.$refs.turntable.style.transform = '';
                if (!isNaN(this.luckyId)) {
                    this.preSlowDown();
                }
            }
        },
    },
};
</script>

